div .mainWrapper {
  height: 500px;
  column-count: 2;
  padding: 15px 15px 0px 15px;
  background-position: bottom right;
  background-size: 100% auto;
  background-repeat: no-repeat;
}

@media (min-width:0px) and (max-width:576px) {
  div .mainWrapper {
    overflow: hidden;
    overflow-y: scroll;
    height: 97%;
    column-count: 1;
  }
}

@media (min-width:576px) and (max-width:992px) {
  div .mainWrapper {
    height: 500px;
    column-count: 2;
  }
}