/* 
 * Basic style rules that apply to the entire React app
 */

 @font-face {
  font-family: 'Roboto';
  src: local('Roboto'), url('./components/media/fonts/RobotoCondensed-Regular.ttf') format('truetype');
}

html,
body {
  font-family: 'Roboto';
  padding: 0;
  margin: 0;
}

body {
  background-color: black;
}

a {
  color: aquamarine
}

a:hover {
  color: white
}

a:visited {
  color: aqua
}